'use client';
import { motion } from "framer-motion";
import React from "react";
import { TextGenerateEffect } from "./ui/text-generate-effect";

export const Introduction: React.FC = () => {
  return (
    <motion.div
      initial={{
        opacity: 0,
        y: -80,
      }}
      animate={{
        opacity: 1,
        y: 0,
      }}
      transition={{
        duration: 0.6,
      }}
      className="w-full py-16 text-[#0b221d] text-center"
    >
      <div className="max-w-3xl mx-auto">
        <motion.h2
          className="text-2xl font-bold font-serif mb-6 md:text-5xl"
          initial={{ scale: 0.8, opacity: 0 }}
          animate={{ scale: 1, opacity: 1 }}
          transition={{ duration: 0.8 }}
          whileInView={{
            opacity: 1,
            scale: 1,
            transition: { duration: 0.8 },
          }}
          viewport={{ once: true }}
        >
          Who We Are
        </motion.h2>

        <motion.p
          className="font-serif pt-3 tracking-wide md:text-xl text-[#083d31] leading-relaxed"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 1, delay: 0.2 }}
          whileInView={{
            opacity: 1,
            transition: { duration: 1 },
          }}
          viewport={{ once: true }}
        >
          Our mission is to empower communities, save lives, and advocate for positive change. 
          With countless impactful projects, we have made a significant difference globally, 
          reaching millions and addressing critical challenges.
        </motion.p>
      </div>
    </motion.div>
  );
};
